// /*  */
// .slick-arrow {}
.customers__slider {
    position: relative;
}

.customers__slider .slick-arrow {
    position: absolute;
    top: 50%;
    //  left: 50%;
    z-index: 5;
}

// /*   */
// .slick-arrow.slick-prev {}
.customers__slider .slick-arrow.slick-prev {
    right: 0;
}

// /*   */
// .slick-arrow.slick-next {}
.customers__slider .slick-arrow.slick-next {
    left: 0;
}

// /*    */
// .slick-arrow.slick-disabled {}
// /*  () */
// .slick-dots {}
// .slick-dots li {}
.customers__slider .slick-dots {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
}

.customers__slider .slick-dots li {
    list-style: none;
    margin: 0 9px;
}

.customers__slider .slick-dots button {
    border: 0;
    font-size: 0;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: #d9d9d9;
}

.customers__slider .slick-dots li.slick-active button {
    background-color: transparent;
    background: no-repeat center/100% url(../images/customers/Vector.png );
    border-radius: 0%;

    @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: url(../images/customers/Vector@2x.png);
    }
}

