.title {
        color: rgba(212, 20, 67, 1);
        font-family: 'Titan One';
        font-style: normal;
        font-weight: 400;
        font-size: 34px;
        line-height: 1.14;
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;

        @media screen and(min-width:768px) {
                font-size: 48px;
                line-height: 1.15;

        }

        @media screen and (min-width:1200px) {
                font-size: 58px;
                line-height: 1.14;
                letter-spacing: 0.06em;
        }

}
