@import "https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Titan+One&display=swap";
.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --primary-color: #fff;
  --first-part: #ffb8ca;
  --second-part: #f0d1a5;
  --third-part: #c2e297;
  --customers-part: #fafafa;
  --customers-part: #fafafa;
  --customers-text-bgc: #e1e1e1;
  --customers-text-color: #000;
  --customers-dot-bgc: #c7133e;
  --gradient-color: #fff5f6;
  --basic-color: #fff;
  --card-cafe: #f0d1a5;
  --card-food: #c2e297;
  --border: #e1e1e1;
  --color-text: #907e82;
  --color-day: #000;
  --color-time: #ffa5ba;
  --general-bg-color: #fff5f6;
  --button-color: #fff;
  --color-accent-btn: #d41443;
  --button-bg-accent: #d41342;
  --subtitle-color: #ffa5ba;
  --soclinks-bg-color: #fea5bb;
  --link-color: #000;
  --copyright-color: #907e82;
  --color-bg-hover: #ecebeb;
  --color-line: #e1e1e1;
}

html {
  box-sizing: border-box;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

body {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  background: var(--primary-color);
  color: var(--primary-color);
  letter-spacing: .03em;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
}

h1, h2, h3, ul, p {
  margin: 0;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.link {
  color: currentColor;
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

a, button {
  cursor: pointer;
  color: inherit;
  font-style: normal;
  text-decoration: none;
}

.button {
  border-color: rgba(0, 0, 0, 0);
  outline: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

@keyframes float {
  50% {
    transform: translate(0, 20px);
  }
}

.buy-form {
  width: 100%;
  padding: 40px;
}

.buy__group {
  flex-direction: column;
  margin-bottom: 10px;
  display: flex;
}

.buy__group:focus-within {
  color: #d41443;
}

.buy__label {
  margin-bottom: 4px;
}

.buy__area {
  position: relative;
}

.buy__input {
  width: 100%;
  color: gray;
  cursor: pointer;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  padding: 11px 11px 11px 42px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.buy__input:focus {
  border: 1px solid #d41443;
  outline: rgba(0, 0, 0, 0);
}

.buy__input::-ms-input-placeholder {
  letter-spacing: .04em;
  color: rgba(117, 117, 117, .5);
  font-size: 13px;
  line-height: 1.16;
}

.buy__input::placeholder {
  letter-spacing: .04em;
  color: rgba(117, 117, 117, .5);
  font-size: 13px;
  line-height: 1.16;
}

.buy__input:not(:placeholder-shown):focus:valid {
  color: green;
  border-color: green;
}

.buy__input:not(:placeholder-shown):focus:invalid {
  color: red;
  border-color: red;
}

.buy__icon {
  width: 18px;
  height: 18px;
  fill: currentColor;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

.buy__area:focus-within .buy__icon {
  fill: #d41443;
}

.buy__commit {
  flex-direction: column;
  margin-bottom: 20px;
  display: flex;
}

.buy__commit:focus-within {
  color: #d41443;
}

.buy__textarea {
  width: 100%;
  color: gray;
  resize: none;
  cursor: pointer;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  padding: 12px 16px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.buy__textarea::-ms-input-placeholder {
  letter-spacing: .04em;
  color: rgba(117, 117, 117, .5);
  font-size: 14px;
  line-height: 1.16;
}

.buy__textarea::placeholder {
  letter-spacing: .04em;
  color: rgba(117, 117, 117, .5);
  font-size: 14px;
  line-height: 1.16;
}

.buy__textarea:focus {
  border: 1px solid #d41443;
  outline: rgba(0, 0, 0, 0);
}

.buy__group--label {
  width: 100%;
  margin-bottom: 10px;
}

.buy__select {
  color: #907e82;
  cursor: pointer;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background: url("Vector-down-pink.34e4b5fb.png") 96% / 10px no-repeat;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  padding: 11px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.buy__select:focus {
  border: 1px solid #d41443;
  outline: rgba(0, 0, 0, 0);
}

@media screen and (min-width: 768px) {
  .buy__select {
    background: url("Vector-down-pink.34e4b5fb.png") 98% / 10px no-repeat;
  }
}

.buy__optgroup {
  color: #907e82;
  font-size: 16px;
}

.policy-block {
  cursor: pointer;
  justify-content: center;
  align-items: baseline;
  margin-bottom: 30px;
  display: flex;
}

.policy__icon:before {
  content: "";
  width: 16px;
  height: 15px;
  vertical-align: middle;
  border: 2px solid rgba(33, 33, 33, .2);
  border-radius: 2px;
  margin-right: 7px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.checkbox:checked + .policy__icon:before {
  vertical-align: middle;
  background-color: #d41443;
  background-image: url("form-check.7adc69b8.svg");
  background-size: contain;
  background-origin: border-box;
  border-color: #d41443;
}

.policy__text {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.16;
}

.policy__link {
  color: #d41443;
}

.btn-block {
  text-align: center;
}

.buy-form__btn {
  min-width: 104px;
  height: 40px;
  color: #ecebeb;
  text-align: center;
  cursor: pointer;
  background: #d41443;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 20px;
  padding: 0 37px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.286;
}

.buy-form__btn:hover, .buy-form__btn:focus {
  color: #d41443;
  background-color: #ecebeb;
  border: 1px solid #d41443;
  outline: rgba(0, 0, 0, 0);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.buy-form__btn:disabled {
  color: #907e82;
  background-color: #e5e5e5;
  border: 1px solid rgba(0, 0, 0, 0);
}

.backdrop {
  z-index: 99999;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(255, 184, 202, .88);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

@media screen and (max-width: 767px) {
  .backdrop {
    padding: 15px;
  }
}

@media screen and (max-width: 1199px) {
  .backdrop {
    overflow-y: scroll;
  }
}

.modal {
  background-color: #fff;
  border-radius: 15px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.5);
}

@media screen and (max-width: 767px) {
  .backdrop.is-hidden .modal {
    transform: scale(.5);
  }
}

@media screen and (min-width: 768px) {
  .modal {
    width: 70vw;
    height: auto;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 10%)scale(1);
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 40vw;
    height: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);
