//  

.products__card--three:hover::before,
.products__card--three:focus::before {
    transform: translate(-50%, -50%) scale(90%);
}

.products__card--three:hover .product__text,
.products__card--three:focus .product__text {
    transform: scale(1.2, 1.2);

}

.products__card--three:hover .product__title,
.products__card--three:focus .product__title {
    transform: translateY(-20px);

}

.products__card--two:hover::before,
.products__card--two:focus::before {
    transform: translate(-50%, -50%) scale(90%);
}

.products__card--two:hover .product__text,
