.button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin-top: 32px;
    margin-bottom: 40px;
    margin-left: auto;
    margin-right: auto;

    border-radius: 50%;
    border-color: transparent;
    background-color: var(--primary-color);
    transition: transform 250ms linear;

    &:hover,
    &:focus {
        transform: scale(1.5, 1.5);
    }

    @media screen and(min-width:1200px) {
        margin-bottom: 60px;
        margin-top: 60px;

    }

}


.button__icon {
    width: 20px;
    height: 20px;
    fill: rgba(212, 20, 67, 1);

}