.contacts__wrapper {
 
    flex-direction: column;

    @media screen and (min-width: 768px) {
        display: flex;
        flex-direction: row;
        margin-left: -16px;
        margin-right: -16px;
    }
}

.contacts {
    background-image: linear-gradient(to top, var(--gradient-color) 25%, var(--basic-color) 25%);

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        background-image: linear-gradient(to top, var(--gradient-color) 54%, var(--basic-color) 54%);
    }

    @media screen and (min-width: 1200px) {
        background-image: linear-gradient(to top, var(--gradient-color) 76%, var(--basic-color) 76%);
    }
}

.contacts__card {
    background: var(--basic-color);
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    max-width: 280px;
    overflow: hidden;
    object-fit: cover;
    transition: scale 250ms cubic-bezier(0, 0, 0, 1);
    padding: 40px 32px;

    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;

    @media screen and (min-width: 768px) {
        max-width: 224px;
        padding: 40px 32px;
        margin-bottom: 0;
    }

    @media screen and (min-width: 1200px) {
        max-width: 368px;
        padding: 40px;
    }
}

// .contacts__card:hover,
// .contacts__card:focus {
//     scale: (1.1);
// }

//  
.card__title {
    display: flex;
    align-items: center;
    justify-content: center;

    line-height: 1.5;
    letter-spacing: 0.04em;
    color: var(--basic-color);
    margin-bottom: 20px;
    font-size: 12px;

    @media screen and (min-width: 768px) {
        font-size: 16px;
        margin-bottom: 32px;
        line-height: 1.87;
    }
    @media screen and (min-width: 1200px) {
        line-height: 1.64;
    }
}

.frame__cafe {
    background: var(--card-cafe);
    border-radius: 6px;
    width: 80px;
    height: 16px;

    @media screen and (min-width: 768px) {
        width: 73px;
        height: 28px;
    }
}

.frame__food {
    background: var(--card-food);
    border-radius: 6px;
    width: 130px;
    height: 16px;

    @media screen and (min-width: 768px) {
        width: 138px;
        height: 28px;
    }
}

.frame__cafe-two {
    background: var(--card-cafe);
    border-radius: 6px;
    width: 80px;
    height: 16px;

    @media screen and (min-width: 768px) {
        width: 73px;
        height: 28px;
    }
}

//  
.border {
    border-bottom: 1px solid var(--border);
    max-width: 216px;
    margin-bottom: 24px;

    @media screen and (min-width: 768px) {
        max-width: 160px;
        margin-bottom: 32px;
    }

    @media screen and (min-width: 1200px) {
        max-width: 288px;
    }
}

.card__city {
    line-height: 1.83;

    letter-spacing: 0.04em;
    margin-bottom: 8px;
    color: var(--color-day);
    font-size: 12px;

    @media screen and (min-width: 768px) {
        font-size: 14px;
        margin-bottom: 12px;
        line-height: 1.86;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
        margin-bottom: 8px;
        line-height: 1.87;
    }
}

.card__text {
    max-width: 179px;
    font-size: 12px;
    line-height: 1.5;

    letter-spacing: 0.04em;
    margin-bottom: 24px;
    color: var(--color-text);

    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 1.64;
        max-width: 159px;
        margin-bottom: 32px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
        max-width: 243px;
        margin-bottom: 32px;
        line-height: 1.81;
    }
}

//  

.card__day {
    line-height: 1.83;
    font-size: 12px;
    letter-spacing: 0.04em;
    color: var(--color-day);

    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 1.86;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
        line-height: 1.87;
    }
}

.card__time {
    font-size: 12px;
    line-height: 1.83;
    color: var(--color-time);

    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 1.86;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
        line-height: 1.87;
    }
}

//      
.retreat {
    margin-bottom: 8px;

    @media screen and (min-width: 768px) {
        margin-bottom: 12px;
    }
}

//   
.border__retreat {
    margin-bottom: 24px;

    @media screen and (min-width: 768px) {
        margin-bottom: 32px;
    }
}

//  
.card__data {
    font-size: 12px;
    line-height: 1.5;
    color: var(--color-text);

    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 1.86;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
        line-height: 1.81;
    }
    &:focus,
    &:hover {
        cursor: pointer;
        color: var(--color-time);

    }
}
